*{
  box-sizing: border-box;
}

body {
  min-height: 100vh;
  display: flex;
  font-weight: 400;
  font-family: sans-serif;
  background: #c0d6ee;  
  background: -webkit-linear-gradient(to right,#f4f7fa,#f4f7fa,#f4f7fa ); 
  background: linear-gradient(to right, #f4f7fa,#f4f7fa,#f4f7fa ); 
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.loginBackground {
  /* fallback for old browsers */
  background: #fcfcfc;

  /* Chrome 10-25, Safari 5.1-6 */
  background: -webkit-linear-gradient(to right, rgba(252, 252, 252, 0.5), rgba(0, 0, 0, 0.5));

  /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  background: linear-gradient(to right, rgba(252, 252, 252, 0.5), rgba(0, 0, 0, 0.5))
}

.adminButtonApprove {
  color:"white";
  border-radius:24px;
  padding: 3px ;
  /* fallback for old browsers */
  background: #84fab0;

  /* Chrome 10-25, Safari 5.1-6 */
  background: -webkit-linear-gradient(to right, rgba(97, 245, 151, 0.5), rgba(107, 206, 255, 0.5));

  /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  background: linear-gradient(to right, rgba(97, 245, 151, 0.5), rgba(107, 206, 255, 0.5))
}


.adminButtonRej {
  border-radius: 24px;
  padding: 3px ;
  /* fallback for old browsers */
  background: #a18cd1;

  /* Chrome 10-25, Safari 5.1-6 */
  background: -webkit-linear-gradient(to right, rrgba(148, 121, 210, 0.5), rgba(244, 139, 214, 0.5));

  /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  background: linear-gradient(to right, rgba(148, 121, 210, 0.5), rgba(244, 139, 214, 0.5));
}

.dotGreen {
  height: 9px;
  width: 9px;
  background-color:#1DE9B6;
  border-radius: 50%;
  display: inline-block;
}
.pLogin {
  margin-top: 10px;
  margin-bottom: 100px;
  margin-right: 150px;
  margin-left: 50px;
  font-weight: bold;
  font-size: 45px;
}

.imgLogin {
  margin-top: 100px;
}

.dotRed {
  height: 9px;
  width: 9px;
  background-color: #F44236;
  border-radius: 50%;
  display: inline-block;
}

.dashboardCard1 {
  /* fallback for old browsers */
  background: #fcfcfc;

  /* Chrome 10-25, Safari 5.1-6 */
  background: -webkit-linear-gradient(to right, rgba(94, 120, 232, 0.6), rgba(117, 72, 163, 0.6));

  /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  background: linear-gradient(to right, rgba(66, 66, 66, 1), rgba(0, 0, 0, 1))
}

.dashboardCard2 {
  /* fallback for old browsers */
  background: #fcfcfc;

  /* Chrome 10-25, Safari 5.1-6 */
  background: -webkit-linear-gradient(to right, rgba(108, 55, 109, 1), rgba(63, 13, 125, 1));

  /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  background: linear-gradient(to right, rgba(108, 55, 109, 1), rgba(63, 13, 125, 1))
}
.dashboardCard3 {
  /* fallback for old browsers */
  background: #fcfcfc;

  /* Chrome 10-25, Safari 5.1-6 */
  background: -webkit-linear-gradient(to right, rgba(94, 120, 232, 0.6), rgba(117, 72, 163, 0.6));

  /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  background: linear-gradient(to right, rgba(87, 38, 201, 1), rgba(176, 0, 179, 1))
}

.progressbarComplete { 
  background: #84fab0;

  /* Chrome 10-25, Safari 5.1-6 */
  background: -webkit-linear-gradient(to right, rgba(97, 245, 151, 0.5), rgba(107, 206, 255, 0.5));

  /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  background: linear-gradient(to right, rgba(97, 245, 151, 0.5), rgba(107, 206, 255, 0.5))
}

.progressbarDelete {
  background: #a18cd1;

  /* Chrome 10-25, Safari 5.1-6 */
  background: -webkit-linear-gradient(to right, rgba(148, 121, 210, 0.5), rgba(244, 139, 214, 0.5));

  /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  background: linear-gradient(to right, rgba(148, 121, 210, 0.5), rgba(244, 139, 214, 0.5))
}

.progressbarPending {
  background: #84fab0;

  /* Chrome 10-25, Safari 5.1-6 */
  background: -webkit-linear-gradient(to right, rgba(97, 245, 151, 0.5), rgba(107, 206, 255, 0.5));

  /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  background: linear-gradient(to right, rgba(97, 245, 151, 0.5), rgba(107, 206, 255, 0.5))
  
}
.UpArrow {
  background: 
  linear-gradient(-45deg,
    rgbargba(132, 250, 176, 0.5),  25%,transparent 25%, 
    transparent 50%, rgba(143, 211, 244, 0.5) 50%, 
    rgba(255, 255, 255, 0.15) 75%,transparent 75%) 
  left/30px 30px repeat-x,
  linear-gradient(to right, rgba(132, 250, 176, 0.5), rgba(143, 211, 244, 0.5)) left/var(--p,100%) fixed,
  lightgray;
box-shadow:inset 0px -2px 5px rgba(0, 0, 0, 0.5);
animation: change 1s linear infinite;
}

body, html, .App, #root, .outer {
  width: 100%;
  height: 100%;
}

.navbar-light {

  background-color: #455470;
  box-shadow: 0px 14px 80px rgba(34, 35, 58, 0.2);
}

.outer {
  display: flex;
  justify-content: center;
  flex-direction: column;
  text-align: left;
}

.inner {
  width: 450px;
  margin: auto;
  
  box-shadow: 0px 14px 80px rgba(34, 35, 58, 0.2);
  padding: 40px 55px 45px 55px;
  border-radius: 15px;
  transition: all .3s;
}

.outer .form-control:focus {
  border-color: #167bff;
  box-shadow: none;
}

.outer h3 {
  text-align: center;
  margin: 0;
  line-height: 1;
  padding-bottom: 20px;
}

.custom-control-label {
  font-weight: 400;
}

.forgot-password,
.forgot-password a {
  text-align: right;
  font-size: 13px;
  padding-top: 10px;
  color: #7f7d7d;
  margin: 0;
}

.forgot-password a {
  color: #167bff;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
